.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.btn {
  background-color: #0d6efd;
  cursor: pointer;
  width: 80px;
  height: 36px;
  line-height: 36px;
  color: white;
  text-align: center;
  display: inline-block;
  border-radius: 5px;
}

.btn-Login {
  position: absolute;
  right: 20px;
  top: 20px;
}

.btn:hover {
  background-color: #0b5ed7;
}

.username {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 32px;
  color: white;
}

.balance {
  position: absolute;
  left: 20px;
  top: 20px;
  font-size: 32px;
  color: white;
}

.balance span,
.username span {
  color: red;
  font-weight: bold;
}

.search-board {

  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}

.boardname {
  width: 300px;
  height: 32px;
  font-size: 16px;
  padding: 0 10px;
  margin-right: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c3400;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
